import React, { useState } from "react";
import { Header, Segment, Menu, Divider } from "semantic-ui-react";

import ContentArtworkPhotosynthetic from "./content-artwork-photosynthetic.component";
import ContentArtworkSpaghettification from "./content-artwork-spaghettification.component";

function ContentArtwork() {
    const [item, setItem] = useState("photosynthetic");

    const handleItemClick = (e, { name }) => setItem(name);

    return (
        <div id="artwork" style={{ marginTop: 100 }}>
            <Header as="h2" inverted>
                Artwork
                <Header.Subheader>
                    Collections of digital artworks in various styles and mediums, available as NFT originals and prints
                </Header.Subheader>
            </Header>
            <Divider hidden />
            <Menu attached="top" tabular>
                <Menu.Item
                    name="photosynthetic"
                    onClick={handleItemClick}
                    active={item === "photosynthetic"}
                    style={item === "photosynthetic" ? null : { color: "white" }}
                />
                <Menu.Item
                    name="spaghettification"
                    onClick={handleItemClick}
                    active={item === "spaghettification"}
                    style={item === "spaghettification" ? null : { color: "white" }}
                />
            </Menu>
            <Segment attached="left" style={{ padding: 28, height: 740 }}>
                {item === "photosynthetic" ? <ContentArtworkPhotosynthetic /> : null}
                {item === "spaghettification" ? <ContentArtworkSpaghettification /> : null}
            </Segment>
            <Divider hidden clearing />
        </div>
    );
}

export default ContentArtwork;
