import React, { useState } from "react";
import { Header, Segment, Menu, Divider } from "semantic-ui-react";

import ContentPropertyCrystalBrigade from "./content-property-crystal-brigade.component";

function ContentProperties() {
    const [item, setItem] = useState("crystal brigade");

    const handleItemClick = (e, { name }) => setItem(name);

    return (
        <div id="properties" style={{ marginTop: 140 }}>
            <Header as="h2" inverted>
                Properties
                <Header.Subheader>
                    Creative content properties in development intended for public release or sale
                </Header.Subheader>
            </Header>
            <Divider hidden />
            <Menu attached="top" tabular>
                <Menu.Item
                    name="crystal brigade"
                    onClick={handleItemClick}
                    active={item === "crystal brigade"}
                    style={item === "crystal brigade" ? null : { color: "white" }}
                />
            </Menu>
            <Segment attached="left" style={{ padding: 28, height: 329 }}>
                <div style={item === "crystal brigade" ? null : { display: "none" }}>
                    <ContentPropertyCrystalBrigade />
                </div>
            </Segment>
            <Divider hidden clearing />
        </div>
    );
}

export default ContentProperties;
